html,body{
  overflow: hidden;
}
.drawer {
  height: 100vh;
  width: 200px;
  background-color: blueviolet;
  position: absolute;
  top: 0;
  transition: 0.4s all;
  right: 100%;
  z-index: 999;
}
.drawer button {
  z-index: 1004;
  position: absolute;
  top: 0;
  right: -95px;
}
.drawer.opened {
  left: 0;
}